.chat {
    padding-top: 2rem;
    .chat_hd {
        width: 100%;
        box-sizing: border-box;
        height: 2rem;
        background: #fff;
        border-bottom: 1px solid #eee;
        position: fixed;
        top: 0;
        .chat_back {
            height: 0.8rem;
            text-align: center;
            i {
                font-size: 0.4rem;
                position: absolute;
                left: 5%;
                top: 0.2rem;
            }
            h2 {
                width: 100%;
                font-size: 0.28rem;
                line-height: 0.8rem;
            }
        }
        .chat_order {
            height: 1rem;
            padding: 0 5%;
            display: flex;
            justify-content: space-around;
            align-items: center;
            img {
                width: 1.2rem;
                height: 0.8rem;
                border: 1px solid #eee;
            }
            .chat_tit {
                width: 65%;
                h2 {
                    font-size: 0.29rem;
                    line-height: 0.5rem;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    white-space: nowrap;
                    span {
                        color: #91D399;
                        margin-right: 0.1rem;
                    }
                }
            }
            h3 {
                font-size: 0.29rem;
                color: #008489;
            }
        }
    }
    .chat_bd {
        height: auto;
        margin: 0.2rem 0 0;
        padding: 0 5%;
        box-sizing: border-box;
        li {
            width: 100%;
            padding: 0 5%;
            box-sizing: border-box;
            border-radius: 5px;
            background: #eee;
            p {
                padding: 0.2rem;
                font-size: 0.28rem;
            }
        }
    }
    button {
        width: 50%;
        height: 0.8rem;
        border: none;
        margin: 0.2rem auto;
        color: #91D399;
        background: none;
        display: inherit;
    }
}